<template lang="pug">
	.title-switch(v-bind:class="{active: assetStatus}")
		p {{ title }}
		.action(v-on:click="toggle()")
			.switch
			.status
				span INACTIVE

</template>

<script>
	export default {
		props: {
			status: Boolean,
			title: String
		},
		data() {
			return {
				assetStatus: this.status
			}
		},
		watch: {
		},
		methods: {
			toggle(){
				this.assetStatus = !this.assetStatus;
				this.$emit('input', this.assetStatus);
			}
		}
	}
</script>